.text {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;

    p {
        margin: 0;
        font-size: 1rem;
        line-height: 140%;
    }

    ul {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 20px;
        margin: 0;
        padding: 0;
        list-style: none;
    }
    
    ul li {
        position: relative;
        display: grid;
        grid-template-columns: 1fr;
        padding-left: 28px;
    
        &::before {        
            content: '';
            position: absolute;
            top: -2px;
            left: 0;
            width: 24px;
            height: 24px;
            background-image: cover;
            background-repeat: no-repeat;
            background-position: center;
            background-image: url(data:image/svg+xml;base64,);
        }
    }
    
    ul + p {
        padding-top: 8px;
    }

    ol {
        display: grid;
        grid-template-columns: 1fr;
        row-gap: 16px;
        margin: 0;
        padding: 0;        
        font-size: 0.875rem;
        list-style: none;
        counter-reset: li;

        li {
            position: relative;
            margin: 0;
            padding: 0;

            &:before {
                content: counter(li) ". ";
                counter-increment: li;
                padding-right: 8px;
                font-weight: 700;
                color: var(--green);
            }
        }
    }
}

.text--three-columns {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 24px;

    div {
        display: grid;
        grid-template-columns: 1fr;
        row-gap: 24px;
    }

    blockquote {
        position: relative;
        margin: 0;
        padding-left: 60px;
        padding-top: 16px;
        padding-right: 16px;
        padding-bottom: 16px;
        min-height: 105px;
        font-style: italic;
        font-weight: 400;
        font-size: 0.875rem;
        line-height: 140%;
        color: var(--gray2);
        background-color: var(--bege);

        &::before {
            content: '';
            position: absolute;
            left: 16px;
            top: 16px;
            display: block;
            width: 28px;
            height: 73px;
            background-image: url('../img/quote.svg');
            background-repeat: no-repeat;
            background-size: cover;
        }
    }

    h3 {
        font-family: 'Rubik';
        font-style: normal;
        font-weight: 500;
        font-size: 1.125rem;
        line-height: 140%;
        color: var(--gray2);
    }

    h3 + p {
        margin-top: -8px;
    }

    img {
        width: 100%;
        height: auto;
    }
}

@media (min-width: $small) {
    .text {
        p {
            font-size: 1.185rem;
        }

        h3 {
            font-size: 1.25rem;
        }

        blockquote {
            padding-left: 100px;
            padding-top: 36px;
            padding-bottom: 36px;
            padding-right: 36px;

            &::before {
                top: 36px;
                left: 36px;
            }
        }

        ol {
            font-size: 1rem;
        }
    }

    .text--three-columns {
        display: flex;
        column-gap: 24px;
        flex-wrap: wrap;
        justify-content: center;
    
        div {
            flex-basis: calc(50% - 12px);
        }
    }
}

@media (min-width: 1000px) {
    .text--three-columns {   
        div {
            flex-basis: calc(100% / 3 - 16px);
            flex-grow: 1;
        }
    }
}

@media (min-width: $desktop-large) {
    .text--three-columns {
        column-gap: 44px;

        div {
            flex-basis: calc(100% / 3 - 30px);
            flex-grow: 1;
        }
    }
}